html, body, #root {
  // Make min-height the entire screen, max the children content
  // Requires flex to be specified for the children flex styles
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  min-height: 100%;
  height: auto;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
